@media(max-width: 1199px) {
    :root {
		--fz: 18px;
	}
    .container {
        padding: 0 100px 0 70px;
    }
    .header{
        &-container {
            padding: 0 100px 0 70px;

        }
        .aside-btn {
            height: 65px;
            right: 35px;
            &::after {
                display: none;
            }
        }
    }
    .section-1{
        .intro-slider {
            &__descr{
                font-size: var(--fz-xl);
            }
            &-link {
                padding: 20px 15px !important;
                
            }
            &-container.section-container.container {
                margin: 0 auto;
                max-width: var(--container-width);
                padding: 0 100px 0 70px ;
            }
            &-item{
                &.active{
                    width: 250px;
                }
            }
        }
        
    }
    .section-5{
        .intro-slider {
            &__descr{
                font-size: var(--fz-m);
            }
            &-link {
                padding: 20px 15px !important;
                
            }
            &-container.section-container.container {
                margin: 0 auto;
                max-width: var(--container-width);
                padding: 0 100px 0 70px ;
            }
            &-item{
                &.active{
                    width: 250px;
                }
            }
        }
        
    }
    .section-2{
        .intro-slider{

            &__wrapper{
                &-left{
                    padding: 0 40px 40px 70px;
                }
            }
        }
        .circle {
            right: 0%;
        }
    }
   
    .section-3{
        &__items {
            column-gap: 20px;
            flex-direction: column;
        }
        &-title,
        &__items,
        &-wrapper,
        &__title{
            margin-bottom: 20px;
        }
        &__item {
            &-title {
                text-align: center;
                margin-bottom: 15px !important;
            }
            p{
                text-align: center;
                margin-bottom: 10px;
                br{
                    display: none;
                }
            }
        }
        &__btn{
            padding: 20px 0;
            width: 250px;
        }
        &__items {
            row-gap: 10px;
        }
        &-wrapper{
            margin-bottom: 40px;
        }
    }
    .section-4{
        &__wrapper{
            &-left{
                padding: 0 40px 0 70px;  
            }
        }
        .swiper-button-next,
        .swiper-button-prev{
            width: 60px;
            height: 60px;
        }
    }
    .section-6{
        &-container {
            padding: 0 40px 0 70px;
        }
    }
    .footer__copyright{
        padding: 40px 40px 40px 70px;
    }
    .swiper-scrollbar{
        display: none !important;
    }
    .aside-btn{
        height: 120px;
    }
    .title::after,
	.section-1 .intro-slider__descr::after {
		top: -35px ;
	}
}

@media (max-width: 1199px ) and (max-height: 900px) {
    .section-6{
        .footer__copyright {
            // margin-top: 10px;
        }
    }
    
}

@media (max-width: 1199px ) and  (max-height: 800px) {
    .section-6{


        .footer__copyright{
            margin-top: 10px;
            padding-top: 30px;
            padding-bottom: 30px;
        }
        &-mail {
            margin-bottom: 20px;
        }
        &__content {
            row-gap: 15px;
        }
        &__office, 
        &__time{
            row-gap: 10px;
        }
        &-title {
            margin-bottom: 20px;
        }
    }
    .title::after,
	.section-1 .intro-slider__descr::after {
		top: -25px ;
	}
}